import React, {PureComponent} from 'react';
import { compose } from 'redux';
import {connect} from 'react-redux';
import {createView} from "../../actions";
import { Link } from "react-router-dom";

class SubmitForm extends PureComponent {


	render() {
		const {
			preview,
			createView,
		} = this.props;


		return <Link className="btn-primary" to="/view" onClick={() => createView(preview)}>Отправить</Link>
	}
}

export default compose(
	connect(({preview}) => ({preview}), dispatch => ({
		createView: value => dispatch(createView(value)),
	}))
)(SubmitForm);




