export const createCard = value => ({
	type: 'CREATE_CARD',
	payload: value
});

export const copyCard = value => ({
	type: 'COPY_CARD',
	payload: value
});

export const updateOrderCards = value => ({
	type: 'UPDATE_ORDER_CARDS',
	payload: value
});

export const createView = value => ({
	type: 'CREATE_VIEW',
	payload: value
});



